<script>
import card1 from "../assets/icon_VISA-01.svg"
import card2 from "../assets/icon_Master-01.svg"
import card3 from "../assets/icon_JCB-01.svg"

import notice from "../assets/icon_question-01.svg"
import warning from "../assets/icon_notice-01.svg"
import hint1 from "../assets/Web_order_hit01@2x.png"
import hint2 from "../assets/Web_order_hit02@2x.png"

import elBtn from '@/components/elBtn.vue'

export default {
    name: 'creditCard',
    components: {
        elBtn
    },
    data: function () {
      return {
          card1: card1,
          card2: card2,
          card3: card3,
          elBtn: elBtn,
          notice: notice,
          warning: warning,
          hint1: hint1,
          hint2: hint2,
          hint:{
              show1 : true,
              show2 : false,
          },
          input:{
              card : false,
              name : false,
              day : false,
              secr : false,
              phone : false,
          },
          verification: false
      }
    },
    methods:{
        showHint(num){
            if(num===1){
            this.hint.show1=!this.hint.show1
            }else{
                this.hint.show2=!this.hint.show2
            }
        },
        close(){
            let card = document.querySelector('#erroCard')
            card.classList.remove('erro-show')
        },
        add(ele){
            if(this.input[ele])return
            let el = document.querySelector("#"+ele)
            el.classList.add('showHint')
        },
        spacing(event){
            let el = document.querySelector('#card')
            if(event.currentTarget.value.length > 19){
                event.currentTarget.value = event.currentTarget.value.slice(0,19)
                return
            }
            event.currentTarget.value = event.currentTarget.value.replace(/\s/g,'').replace(/(\d{4})(?=\d)/g,"$1 ")
            if(event.currentTarget.value.length < 19){
                this.input.card = false
                el.classList.add('showHint')
            }else if(event.currentTarget.value.length == 19 && event.currentTarget.value.search(/\d{4}\s\d{4}\s\d{4}\s\d{4}/) === 0){
                this.input.card = true
                el.classList.remove('showHint')
            }
            this.verifi()
        },
        upper(event){
            let el = document.querySelector('#name')
            if(event.currentTarget.value.toUpperCase() == event.currentTarget.value && event.currentTarget.value.search(/[A-Z]/) > -1 && event.currentTarget.value.search(/[0-9]/) == -1){
                this.input.name = true
                el.classList.remove('showHint')
            }else{
                this.input.name = false
                el.classList.add('showHint')
            }
            this.verifi()
        },
        mayday(event){
            let el = document.querySelector('#day')
            if(event.currentTarget.value.length > 5){
                event.currentTarget.value = event.currentTarget.value.slice(0,5)
                return
            }
            event.currentTarget.value = event.currentTarget.value.replace(/\//g,'').replace(/(\d{2})(?=\d)/g,"$1/")
            if(event.currentTarget.value.length == 5 && event.currentTarget.value.search(/[0-1]\d\/\d{2}/) === 0){
                this.input.day = true
                el.classList.remove('showHint')
            }else{
                this.input.day = false
                el.classList.add('showHint')
            }
            this.verifi()
        },
        security(event){
            let el = document.querySelector('#secr')
            if(event.currentTarget.value.length > 3){
                event.currentTarget.value = event.currentTarget.value.slice(0,3)
                return
            }
            if(event.currentTarget.value.length == 3 && event.currentTarget.value.search(/\d{3}/) === 0){
                this.input.secr = true
                el.classList.remove('showHint')
            }else{
                this.input.secr = false
                el.classList.add('showHint')
            }
            this.verifi()
        },
        cellph(event){
            let el = document.querySelector('#phone')
            if(event.currentTarget.value.length > 10){
                event.currentTarget.value = event.currentTarget.value.slice(0,10)
                return
            }
            if(event.currentTarget.value.length == 10 && event.currentTarget.value.search(/^09\d{8}$/) === 0){
                this.input.phone = true
                el.classList.remove('showHint')
            }else{
                this.input.phone = false
                el.classList.add('showHint')
            }
            this.verifi()
        },
        verifi(){
            this.verification = false
            if(!this.input.card)return
            if(!this.input.name)return
            if(!this.input.day)return
            if(!this.input.secr)return
            if(!this.input.phone)return
            this.verification = true
        }
    }
}
</script>
